import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
/*
  Generated class for the ImagesProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class ImagesProvider {


  private remoteUri = "http://192.168.200.185:80/app/...";// a voir
  private filesList: Array<{}>;
  private fileRead;

  constructor(public http: HttpClient) { }

  handleImageSelection(files: any): Observable<any> {
    this.filesList = [];
    //[].forEach.call(files, this.readAndAddFile);

    for (let file of files) {
      let reader = new FileReader();

      reader.onload = () => {
        this.fileRead = {
          name: file.name,
          size: file.size,
          type: file.type,
          src: reader.result
        }
      }
      reader.readAsDataURL(file);

      reader.onloadend = () => {

        this.filesList.push(this.fileRead)


      }
    }


    return Observable.create((observer) => {

      observer.next(this.filesList);
      observer.complete();

    });

  }

}



